.App {
  text-align: center;
  background-color: black;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.Photo {
  border-radius: 50%;
}

.video-responsive {
  overflow: hidden;
  padding-bottom: 10%;
  position: relative;
  height: 120px;
  width: 213px;
}

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@media (max-width: 1300px) {
  .App-header {
    font-size: 5px;
  }
  .Gif {
    height: 50px;
  }
}

@media (orientation: landscape) {
  .App-header {
    font-size: 5px;
  }
  .Gif {
    height: 50px;
  }
}

.App-header {
  background-color: #282e34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  font-size: 3vw;
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
